.settings {
  position: relative;
  max-width: 400px;
  background: #040404;
  border-radius: 20px;
  color: #fff;
  padding: 20px 20px 0 20px;
  margin: 0 auto;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    top: -220px;
    right: -220px;
    width: 350px;
    height: 350px;
    background: linear-gradient(180.00deg, rgba(0, 194, 255, 0),rgb(255, 41, 195) 100%);
    filter: blur(120.08px);
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    left: -220px;
    bottom: -220px;
    width: 350px;
    height: 350px;
    background: linear-gradient(180.00deg, rgb(23, 59, 128),rgb(17, 87, 62) 100%);
    filter: blur(120.08px);
    pointer-events: none;
  }
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
  }
  &__btn {
    width: 18px;
    flex-shrink: 0;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__search {
    position: relative;
    z-index: 2;
    margin-top: 16px;
    .search-input {
      width: 100%;
      height: 40px;
      font-size: 14px;
      line-height: 120%;
      color: #fff;
      background: transparent;
      border: 1px solid #601DC5;
      border-radius: 8px;
      padding: 0 41px 0 20px;
      &::placeholder {
        color: #fff;
      }
    }
    .search-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      width: 16px;
      background: transparent;
      padding: 0;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
}
.transactions {
  position: relative;
  z-index: 2;
  margin-top: 32px;
  &__items {
    width: calc(100% + 8px);
    height: 440px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    padding-right: 8px;
    margin-top: 20px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
      border-radius: 10px;
      margin-bottom: 20px;
    }
    &::-webkit-scrollbar-thumb {
      background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
      border-radius: 10px;
      margin-bottom: 20px;
    }
  }
  &__item {
    min-height: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    background: rgba(255, 255, 255, 0.06);
    backdrop-filter: blur(289.3px);
    border-radius: 6px;
    padding: 5px 15px;
    &:last-child {
      margin-bottom: 20px;
    }
  }
  &__date {
    font-size: 14px;
    line-height: 105%;
    color: #ACACAC;
  }
  &__usdt {
    font-size: 14px;
    line-height: 105%;
    span {
      color: #E88D23;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 105%;
    color: #23D0E8;
    span {
      color: #ACACAC;
    }
  }
  &__icon {
    width: 15px;
    img {
      width: 100%;
    }
  }
}